import {mapState} from 'vuex';
export default{
    props: ['index'],
    data() {
        return {

        };
    },
    computed: {
        ...mapState(['global'])
    },
    watch: {

    },
    methods: {
        toPayChannelDetails(cardId) {
            if (this.index.isSigned) {
                this.$router.push({path: `/cardInfo/${cardId}`});
            } else {
                this.$router.push({path: `/signingAuthorization/${cardId}`});
            }
        }
    },
    created () {
    }
};



